@value mediaQuery from "../../common/size.css";

.cardSection{
    max-width: 80rem;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    
}

.container{    
    display: flex;
    flex: 1;
    
}



@media screen and (max-width: mediaQuery){
    .container{
        display: block;
    }
}